.textarea {
  outline: none;
  resize: none;
  width: 100%;

  padding: 14px 9px;

  border: 1px solid #c9c9c9;
  border-radius: 5px;

  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 16px;

  color: #161616;
}

.textarea:focus {
  border: 1px solid #2f80ed;
  border-radius: 7px;
}
